import React from 'react';
import 'tachyons';
const Imagelinkform =({InputChange,Click}) =>
{


    return (
        <div>
          <p className='f3'>
            {'This Magic Brain will detect faces in your pictures. Give it a try.'}
          </p>
          <div className='center'>
            <div className='form center pa2 br3 shadow-5'>
              <input className='f4 pa2 w-70 center' type='tex' onChange={InputChange}/>
              <button
                className='w-30 butten grow f4 link ph3 pv2 dib white bg-light-purple'
                onClick={Click}
              >Detect</button>
            </div>
          </div>
        </div>
      );
}

export default Imagelinkform;